.Typist .Cursor--blinking {
  opacity: 1;
  -webkit-animation: blink 1s linear infinite;
  animation: blink 1s linear infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

* {
  margin: 0;
  padding: 0;
  outline: none; }

body, html {
  height: 100%;
  width: 100%;
  background-color: #202020;
  color: white; }

.invert {
  -webkit-filter: brightness(0) invert(1);
  filter: brightness(0) invert(1); }

.clickable {
  cursor: pointer; }

.btn {
  transition: background 0.2s ease-in-out; }

.grabbable {
  cursor: grab; }
  .grabbable:focus, .grabbable:active {
    cursor: grabbing; }
